* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

#search-box:focus {
 outline-color:midnightblue
} 
.country-list{
  list-style: none;
  padding: 0;
  margin: 0 0 20px 0;
}
.country-item{
  display: flex;
}
.country-item:not(:last-child) {
  margin-bottom: 20px;

}
.country-img{
  margin-right: 20px;
}
.country-name{
  font-size: 18px;
  margin: 0;
}
.country-info-data{
  display: inline-block;
  font-size: 18px;
  font-weight: 500;
  margin-right: 10px;
  margin-bottom: 20px;
}
